class FlowWrapper extends Component {
    constructor(props){
        super(props);
        this.state = {
            radioValue:"通过",
            showLabelModal:false,
            showDrawer:false,//节点详情抽屉弹窗
            currentEdge:{},//当前选择的线信息
            currentNode:{},//当前选择的节点信息
            action:"add",//新增/修改
            flowData:null,
            nodeDetails:[]
        }
    }
    componentDidMount(){
        const searchParamsObj = this.props.info;
        try{
            if(searchParamsObj.workFlowId){
                this.getFlowDataById(searchParamsObj.workFlowId)
            }else{
                switch(searchParamsObj.type){
                    case "1"://创建计划
                        this.setState({flowData:initNode.plan});
                        return false;
                    case "2"://表单创建
                        this.setState({flowData:initNode.addForm});
                        return false;
                    default://表单填报
                        this.setState({flowData:initNode.fillForm});
                }
            }
        } catch (error){
            console.log(error)
        }
    }
    getFlowDataById = (id) => {
        if(id){
            getAction(`/ybt/wf/process/queryProcessById?processId=${id}`).then(res => {
                if(res.success){
                    let data = JSON.parse(res.obj)
                    this.setState({
                        flowData:{
                            nodes:data.nodeList,
                            edges:data.edgeList
                        },
                        nodeDetails:data.nodeDetails
                    });
                    this.props.updateNodeDetails(data.nodeDetails);
                }else{
                    message.error("获取数据出错")
                }
            })
        }
    }
    //拖动节点
    onNodeDragStart = (e) => {
        const judgeIds = ["n001","n002","n003","n004","e001","e002"];
        const model = e.item.getModel();
        const graph = e.item.getGraph();
        if(judgeIds.indexOf(model.judgeId)!==-1){
            console.log("内置节点不可操作---拖动");
            graph.emit('drop');
        }
    }
    //点击节点
    onClick = (e) => {
        const judgeIds = ["n001","n002","n003","n004","e001","e002"];
        if(!e.item){
            return false
        }
        const model = e.item.getModel();
        if(judgeIds.indexOf(model.judgeId)!==-1){
            console.log("内置节点不可操作---点击");
        }else{
            // this.setState({showDrawer:true})
            if(e.item.type==="node"){//点击了节点 显示抽屉
                 this.setState({showDrawer:true,currentNode:e.item})
            }else if(e.item.type==="edge"){//点击了线 显示弹窗
                this.setState({
                    showLabelModal:true,
                    action:"click",
                    radioValue:e.item.getModel().label,
                    currentEdge:e.item,
                })
            }
        }
    }
    //判断是否新增连线
    onAfterChange = (e) => {
        if(e.item && e.item.type==="edge" && e.action === "add"){//新建连线
            this.setState({
                currentEdge:e.item,
                showLabelModal:true,
                action:"add",
                radioValue:"通过"
            })
        }
    }
    //更新图上线的属性
    apiAction = () => {
        const { propsAPI } = this.props;
        const { executeCommand, update } = propsAPI;
        executeCommand(() => {
            update(this.state.currentEdge, { label:this.state.radioValue });
        });
    }
    changeRadio = (e) => {
        this.setState({radioValue:e.target.value})
    }
    onOk = () => {
        this.setState({
            showLabelModal:false,
        },this.apiAction)
    }
    onCancel = () => {
        this.setState({
            showLabelModal:false
        })
    }
    onDelete = () => {//删除
        this.setState({
            showLabelModal:false,
        },()=> {
            const { propsAPI } = this.props;
            const { executeCommand, remove } = propsAPI;
            executeCommand(() => {
                remove(this.state.currentEdge);
            });
        })
    }
    //关闭节点属性抽屉
    onCloseDrawer = () => {
        this.setState({ showDrawer: false});
    };
    //更新抽屉弹窗返回的节点详细信息
    updataNodeParams = (params) => {
        const currentNodeId = this.state.currentNode.model.id;
        let nodeDetails = [];
        let addOrEdit = "add";
        this.state.nodeDetails.forEach(item => {
            if(item.id === currentNodeId){//如果当前节点已经存在
                nodeDetails.push({
                    id:currentNodeId,
                    ...params
                });
                addOrEdit = "edit"
            }else{
                nodeDetails.push({
                    ...item
                })
            }
        });
        if(addOrEdit==="add"){
            nodeDetails.push({
                id:currentNodeId,
                ...params
            },)
        }
        this.setState({
            showDrawer:false,
            nodeDetails:nodeDetails
        },()=>{
            const { propsAPI } = this.props;
            const { executeCommand, update } = propsAPI;
            executeCommand(() => {
                update(this.state.currentNode, { label:params.nodeName });
            });
            this.props.updateNodeDetails(this.state.nodeDetails)
        })
    }
    //删除节点（同时删除与改节点相连的线）
    _deleteNode = (id) => {
        //先获取最新的流程数据，然后在当前流程数据基础上进行删除操作，最后重新设置
        const { propsAPI } = this.props;
        const data = propsAPI.save();
        const nodes = data.nodes.filter(item => item.id!==id);
        const edges = data.edges.filter(item => item.target!==id&&item.source!==id);
        this.setState({
            flowData:{
                nodes,
                edges
            },
            showDrawer:false
        })
    }
    render() {
        if(!this.state.flowData){
            return null
        }
        return (
            <React.Fragment>
                <Flow 
                    data={this.state.flowData} 
                    style={{height:700}} 
                    onNodeDragStart={this.onNodeDragStart} 
                    onClick={this.onClick} 
                    onAfterChange={this.onAfterChange}
                    graph={{ edgeDefaultShape: 'flow-polyline'}} 
                />
                <Modal
                    visible={this.state.showLabelModal}
                    bodyStyle={{textAlign:"center"}}
                    width={300}
                    title={"连线设置"}
                    onOk={this.onOk}
                    onCancel={this.onCancel}
                    footer={
                        <div style={{textAlign:"center"}}>
                            <Button onClick={this.onOk} type="primary">确定</Button>
                            {this.state.action==="add"?null:<Button onClick={this.onDelete}>删除</Button>}
                            <Button onClick={this.onCancel}>取消</Button>
                        </div>
                    }
                >
                    <Radio.Group onChange={this.changeRadio} value={this.state.radioValue}>
                        <Radio value="通过">通过</Radio>
                        <Radio value="驳回">驳回</Radio>
                    </Radio.Group>
                </Modal>
                <Drawer
                    className={styles.drawerModal}
                    title="节点属性"
                    width={380}
                    placement="right"
                    closable={false}
                    onClose={this.onCloseDrawer}
                    visible={this.state.showDrawer}
                    destroyOnClose={true}
                    // maskClosable={false}
                >
                    <DrawerModal nodeDetails={this.state.nodeDetails} updataNodeParams={this.updataNodeParams} currentNode={this.state.currentNode} onCloseDrawer={this.onCloseDrawer} deleteNode={this._deleteNode}/>
                </Drawer>
            </React.Fragment>
        )
    }
}

export default withRouter(withPropsAPI(FlowWrapper));
