import React, { Component } from 'react';
import { Button, Radio, Input, Tag, message  } from 'antd';
import styles from './index.module.less';
import ChooseMen from './chooseApprovalMen'
import ChooseRole from './chooseApprovalRole'
export default class DrawerModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            nodeName:"",//节点名称
            userType:"1",//审批人类型1：人员，2：角色
            chooseMenVisible:false,
            chooseRoleVisible:false,
            selectItems:[
                // {"participantId":"1002010334","participantName":"张三"}
            ], //选择的人员或者角色列表
            selectKeys:[], //选择的key
            auditType:"1",//审批类型1：会签，2：或签
            nodeDetails:[],//所有节点信息集合
        }
    }
    componentDidMount(){
        // console.log(this.props.currentNode.model);//当前点击的节点信息
        const currentNodeDetail = this.props.nodeDetails.find(item => item.id===this.props.currentNode.model.id) || {};
        console.log(currentNodeDetail);
        if(JSON.stringify(currentNodeDetail)==="{}"){
            this.setState({
                nodeDetails:this.props.nodeDetails,
                selectItems: [],
                selectKeys:[],
                nodeName:this.props.currentNode.model.label,
                userType:"1",
                auditType:"1",
                nodeId:this.props.currentNode.model.id
            })
        }else{
            this.setState({
                nodeDetails:this.props.nodeDetails,
                selectItems:currentNodeDetail.selectUsers || [],
                selectKeys:Array.isArray(currentNodeDetail.selectUsers)?currentNodeDetail.selectUsers.map(item => item.participantId):[],
                nodeName:currentNodeDetail.nodeName,
                userType:currentNodeDetail.userType,
                auditType:currentNodeDetail.auditType,
                nodeId:this.props.currentNode.model.id
            })
        }
    }
    //修改节点名称
    changeNodeName = (e) => {
        this.setState({nodeName:e.target.value})
    }
    //删除节点
    _deleteNode = () => {
        this.props.deleteNode(this.props.currentNode.model.id);
    }
    //切换审批人类型
    changeUserType = (e) => {
        this.setState({userType:e.target.value})
    }
    //打开编辑人员弹窗
    openUserModal = () => {
        this.setState({
            chooseMenVisible:true
        })
    }
    //打开编辑角色弹窗
    openRoleModal = () => {
        this.setState({
            chooseRoleVisible:true
        })
    }
    //修改审批方式（会签/或签）
    changeAuditType = (e) => {
        this.setState({auditType:e.target.value})
    }
    saveForm = () => {
        if(this.state.nodeName===""){
            message.error("审批节点名称不能为空");
            return false;
        }
        const params = {
            nodeName:this.state.nodeName,
            userType:this.state.userType,
            selectUsers:this.state.selectItems,
            auditType:this.state.auditType
        };
        this.props.updataNodeParams(params)
    }
    handleCancelMen=()=>{
        this.setState({
            chooseMenVisible:false
        })
    }
    handleCancelRole=()=>{
        this.setState({
            chooseRoleVisible:false
        })
    }
    handleOkMen=(selectKeys,selectItems)=>{
        // selectItems = [ {name: "王欢",userId: "4444"} ]
        let items = selectItems.map(item => ({participantName: item.name,participantId: item.userId}))
        this.setState({
            chooseMenVisible:false,
            selectKeys,
            selectItems:items
        },()=>{ })
    }
    handleOkRole=(selectKeys,selectItems)=>{
         // selectItems = [ {roleName: "王欢",roleId: "4444"} ]
         let items = selectItems.map(item => ({participantName: item.roleName,participantId: item.roleId}))
        this.setState({
            chooseRoleVisible:false,
            selectKeys,
            selectItems:items
        })
    }
    //清空
    clearAll = () => {
        this.setState({
            selectKeys:[],
            selectItems:[]
        })
    }
    render() {
        return (
            <React.Fragment>
                <div className={styles.content}>
                    <div className={styles.row}>
                        <div className={styles.label}><i>*</i>节点名称：</div>
                        <Input placeholder="请输入节点名称" style={{width:"240px"}} value={this.state.nodeName} onChange={this.changeNodeName}/>
                    </div>
                    <div className={styles.row}>
                        <div className={styles.label}>设置审批人：</div>
                        <Radio.Group value={this.state.userType} onChange={this.changeUserType} style={{lineHeight:"32px"}}>
                            <Radio value={"1"}>指定人员</Radio>
                            <Radio value={"2"}>角色</Radio>
                        </Radio.Group>
                    </div>
                    <div className={styles.row}>
                        {
                            this.state.userType==="1"?
                            <Button type="primary" icon="plus" onClick={this.openUserModal}>编辑人员</Button>:
                            <Button type="primary" icon="plus" onClick={this.openRoleModal}>编辑角色</Button>
                        }
                        
                    </div>
                    <div className={styles.row}>
                        {
                           this.state.selectItems.map(item => (<Tag key={item.participantId} color="blue">{item.participantName}</Tag>))
                        }
                        {
                            this.state.selectItems.length>0?<span style={{color:"#3F54A2",cursor:"pointer"}} onClick={this.clearAll}>清空</span>:null
                        }
                    </div>
                    <div style={{background:"#E8EBEE",height:1,margin:"16px 0"}}></div>
                    <div className={styles.row}>
                        <p>多人审核时采用的审核方式</p>
                        <Radio.Group onChange={this.changeAuditType} value={this.state.auditType}>
                            <Radio value="2">会签（须所有审核人同意）</Radio>
                            <br/><br/>
                            <Radio value="1">或签（一名审核人同意或拒绝即可）</Radio>
                        </Radio.Group>
                    </div>
                </div>
                <div className={styles.footer}>
                    <Button type="primary" onClick={this.saveForm}>保 存</Button>
                    <Button onClick={this._deleteNode}>删 除</Button>
                    <Button onClick={()=>{this.props.onCloseDrawer()}}>取 消</Button>
                </div>
                {
                    this.state.chooseMenVisible?<ChooseMen handleOk={this.handleOkMen} handleCancel={this.handleCancelMen} chooseMenVisible={this.state.chooseMenVisible}/>:null
                }
                {
                    this.state.chooseRoleVisible?<ChooseRole handleOk={this.handleOkRole} handleCancel={this.handleCancelRole} chooseRoleVisible={this.state.chooseRoleVisible}/>:null
                }
            </React.Fragment>
        )
    }
}
