import React from 'react';
import { withPropsAPI } from 'gg-editor';
import { Button,message } from 'antd';
import { withRouter } from 'react-router-dom'
import styles from './index.module.less';
import { postAction } from '@/axios';

function Footer(props){
    const save = () => {
        const { propsAPI } = props;
        const data = propsAPI.save();
        if(data.edges.length<(data.nodes.length-1)){
            message.error("流程未闭合");
            return false;
        }
        if(data.edges.some(item => (typeof item.target)==="object")){
            message.error("流程未闭合");
            return false;
        }
        let datas = {
            nodeList:data.nodes,
            edgeList:data.edges,
            nodeDetails:props.nodeDetails,
            processName:props.info.name,
            processType:parseInt(props.info.type),
            processFolderId:props.info.folderId,
            // processId:props.info.workFlowId
        }
        if(props.info.workFlowId){
            datas.processId = props.info.workFlowId
        }
        if(props.param && props.param.id){
            datas.status=1
            postAction("/ybt/wf/process/createAllPrccessNode",datas).then(res => {
                if(res.success){
                    message.success("保存成功");
                    props.history.goBack();
                    // if(props.param.url === "formManagement"){
                    //     props.history.push(`/formManagement/setWorkFlow?id=${props.param.id}`)
                    // }else{
                    //     props.history.push("/fillPlan/step4")
                    // }
                }else{
                    message.error(res.obj)
                }
            })
        }else{
            postAction("/ybt/wf/process/createAllPrccessNode",datas).then(res => {
                if(res.success){
                    message.success("保存成功");
                    props.history.goBack();
                    // props.history.push("/systemSettings/workFlowManagement")
                }else{
                    message.error(res.obj)
                }
            })
        }
        
    }
    const cancle = () => {
        // props.history.push("/systemSettings/workFlowManagement");
        props.history.goBack()
    }
    return (
        <div className={styles.footer}>
            <Button type="primary" onClick={save}>保存</Button>
            <Button onClick={cancle}>取消</Button>
        </div>
    )
}

export default withRouter(withPropsAPI(Footer))