import React, { Component } from 'react'
import { Radio,message } from 'antd';
import {postAction} from '@/axios';
import styles from './index.module.less';
class MyTransfer extends Component {
    constructor(props){
        super(props);
        this.state = {
            data:[
                // {roleId:"9901",roleName:"超级管理员"}
            ],
            roleId:"",
            item:{
                // roleId:"",
                // roleName:""
            },
        }
    }

    componentDidMount() {
        this.getData()
    }
    
    getData = () => {
        let param={
            type:"2",
            departId:"",
            name:""
        }
        postAction("/ybt/wf/process/queryAllUserOrRole",param).then(res => {
            if(res.success){
                this.setState({data:res.obj})
            }else{
                message.error("获取信息出错");
                this.setState({data:[]})
            }
        })
    };

    _onChange= (e) => {
        let items=this.state.data.filter(item=>item.roleId===e.target.value);
        this.setState({
            roleId:e.target.value,
            item:items[0]
        });
        this.props.handleSelect([e.target.value],items)
    }

    render() {
        return (
            <div className={styles.container}>
                <div className={styles.bordered}>
                    <span className={styles.title}>选择人员：</span>
                    <div className={styles.list}>
                        <div className={styles.listContent} style={{paddingLeft:20}}>
                            <Radio.Group onChange={this._onChange}>
                                {
                                    this.state.data.length>0 && this.state.data.map(item=>{
                                        return <div key={item.roleId}>
                                                <Radio value={item.roleId} >
                                                    {item.roleName}
                                                </Radio>
                                            </div>
                                    })
                                }
                            </Radio.Group>
                        </div>
                    </div>
                </div>
                <div className={styles.bordered}>
                    <span className={styles.title}>已选人员：</span>
                    <div className={styles.list} style={{paddingLeft:20}}>
                        <div className={styles.listContent}>
                            <div className="list">{this.state.item.roleName}</div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default MyTransfer