import React, { Component } from 'react';
import {Form,TreeSelect,Input,Button} from 'antd';
import { postAction } from '@/axios';

const FormItem = Form.Item;

class Myfilter extends Component {
    state={
        data:[
            // {
            //     departId:"9901",
            //     departName:"职能处室",
            //     children:[
            //         {departId:"99011",departName:"院办"},
            //         {departId:"99012",departName:"教务处"}
            //     ]
            // },{
            //     departId:"9902",
            //     departName:"二级学院"
            // }
        ]
    }
    componentDidMount(){
        this.getDepartMent();
    }
    getDepartMent = () => {
        postAction("/ybt/system/setting/depart/getDepartList").then(res => {
            if(res.success){
                this.setState({
                    data:res.obj
                })
            }
        })
    }
    //渲染部门树形菜单
    renderSelectTree = (departmentList = []) => departmentList.map(item => 
        <TreeSelect.TreeNode key={item.departId} value={item.departId} title={item.departName}>
            {this.renderSelectTree(item.children)}
        </TreeSelect.TreeNode>
    );

    handleClick=()=>{
        this.props.form.validateFields((err, values) => {
            if (!err) {
                this.props.handleSearch(values)
            }
        })
    }
    render() {
        const {getFieldDecorator} =this.props.form
        return (
            <Form layout='inline' style={{marginBottom:15}}>
                <FormItem label='部门'>
                    {
                        getFieldDecorator('departId',{
                            initialValue:''
                        })(<TreeSelect
                            style={{ width: 230 }}
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            placeholder="请选择部门"
                            allowClear
                            treeDefaultExpandAll
                        >
                            {this.renderSelectTree(this.state.data)}
                        </TreeSelect>)
                    }
                </FormItem>
                <FormItem label='姓名/工号'>
                    {
                        getFieldDecorator('name',{
                            initialValue:""
                        })(<Input placeholder='请输入教工姓名/工号'/>)
                    }
                </FormItem>
                <FormItem>
                    <Button onClick={this.handleClick} type='primary' ghost icon='search'>搜索</Button>
                </FormItem>
            </Form>
        )
    }
}

export default Form.create()(Myfilter)
