import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Card } from 'antd';
import GGEditor from 'gg-editor';
import FlowItemPanel from './flowItemPanel';
import Footer from './footer';

import FlowWrapper from './flowWrapper';
import styles from './index.module.less'
import {parseUrlSearch} from './util'
class WorkFlowDesign extends Component {
    constructor(props){
        super(props);
        this.state = {
            nodeDetails:[],//节点信息
            folderId:"",//所属文件夹类型
            type:"",//流程类型
            name:"",//流程名称
            workFlowId:"",
            param:null
        }
    }
    componentWillMount(){
        const searchParamsObj = JSON.parse(window.localStorage.getItem("flowInfo"));
        let param=parseUrlSearch()
        if(param.id){
            this.setState({
                param
            })
        }
        if(searchParamsObj.id){//有id则为修改,直接通过id去请求初始数据和基本信息
            this.setState({
                workFlowId:searchParamsObj.id,
                folderId:searchParamsObj.folderId,
                type:searchParamsObj.type,
                name:searchParamsObj.flowName,
            })
        }else{//无id则为新增
            this.setState({
                folderId:searchParamsObj.folderId,
                type:searchParamsObj.type,
                name:searchParamsObj.flowName
            })
        }
    }
    //通过流程id获取workflow信息
    getWorkFlowInfo = (id) => {
        let res = {
            "processName":"测试流程","processType":1,"processFolderId":"aaa","delFlag":0,
            "nodeList":[
                {"type": "node","size": "70*70","shape": "flow-circle","color": "#FA8C16", "label": "结束节点","x": 55,"y": 55,"id": "6","index": 0,"nodeType":6},
                {"type": "node","size": "70*70","shape": "flow-circle","color": "#FA8C16", "label": "开始节点","x": 55,"y": 55,"id": "1","index": 0,"nodeType":1,"judgeId":"n001"},
                {"type": "node","size": "70*70","shape": "flow-circle","color": "#FA8C16", "label": "发起计划","x": 55,"y": 55,"id": "2","index": 0,"nodeType":2},
                {"type": "node","size": "70*70","shape": "flow-circle","color": "#FA8C16", "label": "审核1节点","x": 55,"y": 55,"id": "3","index": 0},
                {"type": "node","size": "70*70","shape": "flow-circle","color": "#FA8C16", "label": "审核2节点","x": 55,"y": 55,"id": "4","index": 0},
                {"type": "node","size": "70*70","shape": "flow-circle","color": "#FA8C16", "label": "下发填报","x": 55,"y": 55,"id": "5","index": 0,"nodeType":2}
            ],
            "edgeList":[
                {"source": "2","sourceAnchor": 2,"label":"test","target": "3","targetAnchor": 0,"id": "b", "index": 1},
                {"source": "3","sourceAnchor": 2,"label":"test","target": "4","targetAnchor": 0,"id": "c", "index": 1},
                {"source": "5","sourceAnchor": 2,"label":"test","target": "6","targetAnchor": 0,"id": "e", "index": 1},
                {"source": "4","sourceAnchor": 2,"label":"test","target": "2","targetAnchor": 0,"id": "f", "index": 1},
                {"source": "1","sourceAnchor": 2,"label":"test","target": "2","targetAnchor": 0,"id": "a", "index": 1},
                {"source": "4","sourceAnchor": 2,"label":"test","target": "5","targetAnchor": 0,"id": "d", "index": 1}
            ],
            "nodeDetails":[
                {"id":"3","nodeName":"审批一","userType":"1","selectUsers":[{"participantId":"1002010334","participantName":"张三"},{"participantId":"1002010554","participantName":"李四"}],"auditType":"1"},
                {"id":"4","nodeName":"审批二","userType":"1","selectUsers":[{"participantId":"1002010335","participantName":"王五"},{"participantId":"1002010336","participantName":"赵六"}],"auditType":"2"}
            ]
        };
        this.setState({
            nodeDetails:res.nodeDetails,
            folderId:res.processFolderId,
            type:res.processType,
            name:res.processName,
            workFlowId:id,
            nodeList:res.nodeList,
            edgeList:res.edgeList
        })
    }
    updateNodeDetails = (details) => {
        this.setState({
            nodeDetails:details
        })
    }
    
    render() {
        const info = {
            workFlowId:this.state.workFlowId,
            folderId:this.state.folderId,
            type:this.state.type,
            name:this.state.name,
            // nodeList:this.state.nodeList,
            // edgeList:this.state.edgeList,
            nodeDetails:this.state.nodeDetails
        }
        return (
            <GGEditor style={{padding:"20px"}} >
                <Card 
                    title={this.state.name}
                    bodyStyle={{padding:0}}
                >
                    <div className={styles.designContainer}>
                        <FlowWrapper updateNodeDetails={this.updateNodeDetails} info={info}/>
                        <FlowItemPanel/>
                        <Footer param={this.state.param} nodeDetails={this.state.nodeDetails} info={info}/>
                    </div>
                </Card>
            </GGEditor>
        )
    }
}

export default withRouter(WorkFlowDesign)