import React, { Component } from 'react'
import {Modal} from 'antd'
import MyTransfer from './transfer'
import MyFilter from './filter'
export default class Index extends Component {
    state={
        selectKeys:[],
        selectItems:[],
        filterValue:null
    }
    handleSearch=(values)=>{
        this.setState({
            filterValue:values
        })
    }
    handleSelect=(selectKeys,items)=>{
        this.setState({
            selectKeys,
            selectItems:items
        })
    }
    handleOk=()=>{
        this.props.handleOk(this.state.selectKeys,this.state.selectItems)
    }
    render() {
        return (
            <Modal
                visible={this.props.chooseMenVisible}
                title='选择审批人'
                okText='保存'
                cancelText='取消'
                width={700}
                onCancel={this.props.handleCancel}
                onOk={this.handleOk}
                maskClosable={false}
            >
                <MyFilter handleSearch={this.handleSearch}/>
                <MyTransfer selectKeys={this.state.selectKeys} handleSelect={this.handleSelect} filterValue={this.state.filterValue}/>
            </Modal>
        )
    }
}
