import React, { Component } from 'react'
import { message, Checkbox  } from 'antd';
import { postAction } from '@/axios';
import styles from './index.module.less';

const CheckboxGroup = Checkbox.Group;
class MyTransfer extends Component {
    state = {
        allData: [],//全部数据
        filterData:[],//筛选后的数据
        checkAll:false,
        checkedList:[],//选择的key
        checkedItems:[],//选择的详情信息
    };

    componentDidMount() {
        this.getData()
    }
    componentDidUpdate(preProps){
        if(this.props.filterValue && JSON.stringify(preProps.filterValue) !== JSON.stringify(this.props.filterValue)){
            this.getData(this.props)
        }
    }
    getData = (props) => {
        let param={
            type:"1",
            departId:"",
            name:""
        }
        if(props && props.filterValue){
            param.departId = props.filterValue.departId;
            param.name = props.filterValue.name;
        }
        postAction("/ybt/wf/process/queryAllUserOrRole",param).then(res => {
            if(res.success){
                if(!props){//第一次进来，没有props，保存全部data
                    this.setState({
                        allData:res.obj,
                        filterData:res.obj
                    })
                }else{
                    this.setState({
                        filterData:res.obj
                    })
                }
            }else{
                message.error("获取信息出错");
                this.setState({data:[]})
            }
        })
        // this.setState({
        //     data:[
        //         {name:"张三",userId:"9901",departName:"测试部门一"},
        //     ]
        // })
    };

    _onCheckAllChange = (e) => {
        //数据合并
        let checkedList = e.target.checked ? Array.from(new Set(this.state.filterData.map(item=>item.userId).concat(this.state.checkedList))):[];
        this.setState({
            checkedList: checkedList,
            checkAll: e.target.checked,
            checkedItems:this.state.allData.filter(item=>{
                return checkedList.find(i=>i === item.userId)
            })
        },()=>{
            this.props.handleSelect(this.state.checkedList,this.state.checkedItems);
        });
    }
    //单选change
    _onChange = (e ) => {
        if(e.target.checked){//选择
            let checkedList = Array.from(new Set(this.state.checkedList.concat(e.target.value)));
            this.setState({
                checkedList:checkedList,
                checkedItems:this.state.allData.filter(item=>{
                    return checkedList.find(i=>i === item.userId)
                })
            },()=>{
                // console.log(this.state.checkedList,items)
                this.props.handleSelect(this.state.checkedList,this.state.checkedItems);
            })
        }else{
            let checkedList = Array.from(new Set(this.state.checkedList.filter(item=>item!==e.target.value)));
            this.setState({
                checkedList:checkedList,
                checkedItems:this.state.allData.filter(item=>{
                    return checkedList.find(i=>i === item.userId)
                })
            },()=>{
                // console.log(this.state.checkedList,items);
                this.props.handleSelect(this.state.checkedList,this.state.checkedItems);
            })
        }
    }
    render() {
        return (
            <div className={styles.container}>
                <div className={styles.bordered}>
                    <span className={styles.title}>选择人员：</span>
                    <div className={styles.list}>
                        <div className={styles.listTitle}>
                            <Checkbox
                                onChange={this._onCheckAllChange}
                                checked={this.state.checkAll}
                            >全选</Checkbox>
                        </div>
                        <div className={styles.listContent} style={{paddingLeft:20}}>
                            <CheckboxGroup
                                value={this.state.checkedList}
                            >
                                {
                                    this.state.filterData.length>0 && this.state.filterData.map(item=>{
                                        return <div key={item.userId} >
                                                <Checkbox 
                                                    onChange={this._onChange} 
                                                    value={item.userId}
                                                >
                                                {item.name}-{item.departName}
                                                </Checkbox>
                                            </div>
                                    })
                                }
                            </CheckboxGroup>
                        </div>
                    </div>
                </div>
                <div className={styles.bordered}>
                    <span className={styles.title}>已选人员：</span>
                    <div className={styles.list} style={{paddingLeft:20}}>
                        <div className={styles.listContent}>
                            {
                                this.state.checkedItems.map(item=><div className="list">{item.name}-{item.departName}</div>)
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default MyTransfer