import React, { Component } from 'react'
import {Modal} from 'antd'
import MyTransfer from './transfer'
export default class Index extends Component {
    state={
        selectKeys:[],
        selectItems:[],
    }
    
    handleSelect=(selectKeys,items)=>{
        this.setState({
            selectKeys,
            selectItems:items
        })
    }
    handleOk=()=>{
        this.props.handleOk(this.state.selectKeys,this.state.selectItems)
    }
    render() {
        return (
            <Modal
                visible={this.props.chooseRoleVisible}
                title='选择角色'
                okText='保存'
                cancelText='取消'
                width={700}
                onCancel={this.props.handleCancel}
                onOk={this.handleOk}
                maskClosable={false}
            >
                <MyTransfer selectKeys={this.state.selectKeys} handleSelect={this.handleSelect}/>
            </Modal>
        )
    }
}
